.about {
  flex-direction: column;
  margin-top: 3em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.about-name {
  color: #cbacac;
}

.about-role {
  font-size: 1.5rem;
  font-weight: 600;
}

.about-desc {
  font-size: 1rem;
  display: flex;
}

h1 {
  font-size: 1.6rem;
}

.profile-pic{
  max-width: 45%;
  position:relative;
}

.header{
  display: flex;
  flex-direction: row;
  justify-content:space-between;
  align-items:center;
}

@media (max-width: 600px) {
  .app .about {
    align-items: flex-start;
    margin-top: 2em;
  }
}